const isProd = process.env['PROD'] === 'true';
const mongoUrl = `mongodb://${isProd ? process.env['MONGODB_URL'] : 'localhost'}:27017/pbl`;
const redisUrl = isProd ? process.env['REDIS_URL'] : 'localhost';

const services = new Proxy({}, {
    get(target, p, receiver) {
        if (isProd) return `${p}-service:4000`;
        return {
            'user-project': 'localhost:4001',
            'notification': 'localhost:4002',
            'task': 'localhost:4003',
            'resource': 'localhost:4004',
        }[p]
    }
});

export default {
    mongoUrl,
    redisUrl,
    services,
}
